/*     */
.archived-requests-container {
  width: 100%;
  padding: 30px;
  background: #f5f7fa; /*   */
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  overflow: hidden; /*   */
}

/*  */
.archived-title {
  text-align: center;
  color: #34312d;
  margin-top: 50px;
  margin-bottom: 30px;
  font-weight: 700;
  letter-spacing: 1.5px;
  font-size: 32px; /*   */
}

/*    */
.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
}

/*       */
.no-requests-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.no-requests-text {
  font-size: 20px;
  color: #dc3545;
  margin-top: 20px;
  font-weight: bold;
}

/*  */
.archived-requests-table {
  width: 100%;
  margin-bottom: 30px;
  overflow-x: auto; /*      */
}

/*  */
.archived-requests-cards {
  display: none; /*   ,     */
  margin-top: 20px;
}

.request-card {
  background: #ffffff;
  border-radius: 15px;
  padding: 20px; /*   25px */
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
}

.request-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.1);
}

/*    */
.table-text {
  font-size: 14px; /*   16px */
}

/*   */
.worker-tag {
  font-size: 12px; /*   14px */
}

/*   */
.card-title {
  font-size: 18px; /*   22px */
}

/*    */
.card-text {
  font-size: 14px; /*   16px */
}

/*  "" */
/*  "" */
.details-button {
  border: none !important; /*   */
  box-shadow: none !important; /*   */
  background: transparent !important; /*   */
  padding: 0; /*    */
  cursor: pointer; /*    */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px; /*   */
  height: 24px; /*   */
  border-radius: 50%; /*    */
  margin-left: auto; /*    */
  transition: none; /*   */
}

.details-button .anticon {
  font-size: 16px; /*   */
  color: #1890ff; /*   */
}

.details-button:hover,
.details-button:focus {
  background-color: transparent; /*     */
  box-shadow: none; /*     */
  outline: none; /*   */
  transform: none; /*     */
}


/*  */
@media (max-width: 768px) {
  .archived-requests-container {
    padding: 20px;
  }

  .archived-title {
    font-size: 24px; /*   32px */
  }

  .table-text {
    font-size: 12px; /*   */
  }

  .worker-tag {
    font-size: 10px; /*   */
  }

  .card-title {
    font-size: 16px; /*   18px */
  }

  .card-text {
    font-size: 12px; /*   14px */
  }

  .details-button {
    width: 28px; /*   32px */
    height: 28px; /*   32px */
  }

  .details-button .anticon {
    font-size: 14px; /*   16px */
    color: #1890ff; /*   */
  }

  .archived-requests-table {
    display: none; /*     */
  }

  .archived-requests-cards {
    display: block; /*     */
  }

  .request-card {
    padding: 15px; /*   20px */
  }
}

@media (min-width: 769px) {
  .archived-requests-cards {
    display: none; /*     */
  }
}
