/* src/components/css/RequestDetailsPage.css */

/*   "Poppins" */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

/*   */
body {
  background-color: #f8fafb;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  width: 100%;
  overflow-x: hidden;
}

.details-container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
  color: #34312d;
}

.request-title {
  margin-top: 20px;
  font-size: 2.2rem;
  font-weight: 700;
  color: #34312d;
  text-align: center;
  margin-bottom: 30px;
  letter-spacing: 1px;
}

.card {
  background-color: #ffffff;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.1);
  margin-bottom: 25px;
  width: 100%;
  box-sizing: border-box;
}

.details-card {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 30px;
  width: 100%;
  box-sizing: border-box;
}

.block-spacing {
  height: 25px;
}

.info-section,
.manage-section,
.workers-section,
.financial-section {
  flex: 1 1 calc(50% - 20px);
  box-sizing: border-box;
  width: 100%;
}

h2 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #34312d;
  margin-bottom: 20px;
  text-align: center;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #34312d;
  margin-bottom: 15px;
  text-align: center;
}

.info-section p,
.manage-section p {
  font-size: 1rem;
  margin-bottom: 10px;
}

.status-section,
.date-section,
.deadline-section,
.difficulty-urgency-section {
  margin-bottom: 20px;
}

.status-section label,
.date-section label,
.deadline-section label,
.difficulty-urgency-section label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.status-select,
.date-picker {
  width: 100%;
  padding: 10px;
  border-radius: 12px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 1rem;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  color: #34312d;
  transition: border-color 0.3s ease;
}

.status-select:focus,
.date-picker:focus {
  outline: none;
  border-color: #1f4e79;
}

.button {
  background-color: #22333b !important;
  background-image: none   !important;
  box-shadow: none !important;
  color: #ffffff;
  font-size: 1rem;
  padding: 12px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
  margin-top: 12px;
  box-sizing: border-box;
}

.button:hover {
  background-color: #22333b;
}

.save-date-button {
  background-color: #4caf50;
}

.save-date-button:hover {
  background-color: #388e3c;
}

/*       */
.button-group {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.button-group .button {
  flex: 1;
}

.workers-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.worker-item {
  background-color: #f6f8fd;
  padding: 14px;
  border-radius: 12px;
  text-align: center;
  cursor: pointer;
  border: 2px solid transparent;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.worker-item:hover {
  background-color: #dfe5e9;
}

.worker-item.selected {
  background-color: #cdd9e3;
  border-color: #6c63ff;
}

.financial-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.financial-inputs {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.financial-inputs select,
.financial-inputs input {
  flex: 1;
  min-width: 100px;
  padding: 12px;
  border-radius: 12px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
  color: #34312d;
  transition: border-color 0.3s ease;
}

.financial-inputs input:focus,
.financial-inputs select:focus {
  outline: none;
  border-color: #1f4e79;
}

.financial-history h2 {
  margin-bottom: 20px;
}

.financial-records-list {
  width: 100%;
  overflow-x: auto;
}

.financial-record-row {
  display: flex;
  align-items: center;
  padding: 14px;
  border-bottom: 1px solid #e0e0e0;
}

.financial-record-row.income {
  background-color: #e7f7ec;
}

.financial-record-row.expense {
  background-color: #fbe6e8;
}

.financial-record-row div {
  flex: 1;
  min-width: 80px;
  text-align: center;
  font-size: 0.9rem;
}

.record-date {
  flex: 1;
}

.record-type {
  flex: 1;
}

.record-amount {
  flex: 1;
  font-weight: bold;
}

.record-description {
  flex: 2;
}

.balance {
  font-size: 1.2rem;
  color: #34312d;
  text-align: right;
  margin-top: 20px;
}

.not-found {
  text-align: center;
  font-size: 1.2rem;
  color: #ff6347;
  margin-top: 25px;
}

strong {
  color: #34312d;
}

.difficulty-urgency-section {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.difficulty-block,
.urgency-block {
  position: relative;
}

.difficulty-block span,
.urgency-block span {
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  top: 2.5rem;
  left: 0;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  display: flex;
  gap: 10px;
  z-index: 10;
}

.dropdown-menu span {
  cursor: pointer;
}

.dropdown-menu span:hover {
  transform: scale(1.1);
}

.deadline-section p {
  font-size: 1rem;
  color: #d32f2f;
  font-weight: bold;
}

.date-section {
  margin-bottom: 20px;
}

.date-item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f6f8fd;
  border-radius: 8px;
  margin-bottom: 10px;
}

.date-label {
  font-weight: 600;
  color: #34312d;
}

.date-value {
  color: #34312d;
}

@media (max-width: 768px) {
  .details-container {
    max-width: 100%;
  }

  .details-card {
    flex-direction: column;
  }

  .financial-record-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .financial-record-row div {
    text-align: left;
    font-size: 0.9rem;
  }

  .financial-inputs {
    flex-direction: column;
  }

  .balance {
    font-size: 1rem;
    text-align: center;
  }

  .date-picker,
  .status-select {
    font-size: 0.9rem;
  }

  .button {
    font-size: 1rem;
    padding: 10px;
  }

  .block-spacing {
    height: 20px;
  }

  .button-group {
    flex-direction: column;
  }
}
