/*      */
.login-container {
  max-width: 420px;
  margin: 0 auto;
  padding: 40px;
  background-color: #ffffff; /*   */
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.12);
}

/*  */
.login-container .ant-typography {
  color: #333333; /* -  */
  font-weight: 600;
  letter-spacing: 1px;
}

/*   */
.login-form {
  margin-top: 20px;
}

/*     */
.login-form .ant-input,
.login-form .ant-input-password {
  padding: 12px;
  font-size: 1rem;
  border-radius: 8px;
  background-color: #f5f7fa; /*     */
  border: 1px solid #dcdcdc;
  transition: all 0.3s ease;
}

/*      */
.login-form .ant-input:focus,
.login-form .ant-input-password:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 8px rgba(74, 144, 226, 0.2);
}

/*   */
.login-button {
  margin-top: 20px;
  padding: 12px 0;
  background-color: #4a90e2; /*    */
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

/*      */
.login-button:hover {
  background-color: #3a78c2;
  transform: translateY(-2px);
}

/*     */
.login-button:active {
  transform: scale(0.98);
}

/*      */
.switch-button {
  margin-top: 15px;
  color: #4a90e2;
  font-size: 1rem;
  font-weight: 500;
  transition: color 0.3s ease, transform 0.2s ease;
}

/*       */
.switch-button:hover {
  color: #3a78c2;
  transform: translateY(-2px);
}

/*    ""   */
.back-button-desktop {
  margin-bottom: 20px;
  font-size: 1rem;
}

/*    ""    */
.back-button-mobile-container {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}

/*    */
.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/*       */
.mobile-cards-container {
  display: flex;
  flex-direction: column;
}

/*     */
.not-found-title {
  text-align: center;
  font-size: 1.8rem;
  color: #dc3545; /*     */
  margin-top: 50px;
}

/*   */
@media (max-width: 768px) {
  .login-container {
    padding: 20px;
  }

  .login-button,
  .switch-button {
    width: 100%;
  }

  .login-button {
    margin-bottom: 15px;
  }

  /*   ""      */
  .back-button-desktop {
    display: none;
  }

  /*       */
  .mobile-cards-container .details-card {
    margin-bottom: 15px;
  }

  /*       */
  .login-container .ant-typography {
    font-size: 1rem;
  }

  .login-form .ant-input,
  .login-form .ant-input-password {
    font-size: 0.9rem;
  }

  .switch-button {
    font-size: 0.9rem;
  }
}
