.confirmation-container {
  padding: 30px;
  max-width: 900px;
  margin: 40px auto;
  background: linear-gradient(135deg, #f5f5f5, #e1e8f0);
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.confirmation-container:hover {
  transform: scale(1.02);
}

.confirmation-title {
  text-align: center;
  margin-bottom: 40px;
  color: #34312d;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
}

.request-card {
  background: #ffffff;
  border-radius: 12px;
  padding: 25px;
  margin: 20px 0;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.request-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.2);
}

strong {
  color: #333;
}

.request-card p {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #555;
  font-weight: 500;
}

.status-label {
  color: green;
  font-weight: bold;
  font-size: 1.1rem;
}

.confirm-button {
  background-color: #34312d !important;
  color: white;
  border: none;
  background-image: none !important;
  box-shadow: none !important;
  padding: 12px 20px;
  border-radius: 8px !important;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  box-shadow: 0 4px 12px rgba(40, 167, 69, 0.4);
}

.confirm-button:hover {
  background-color: #218838;
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(40, 167, 69, 0.6);
}

.no-requests {
  padding-top: 20px;
  text-align: center;
  font-size: 1.2rem !important;
  color: #34312d;
  margin-top: 60px;
  font-weight: bold;
}

@media (max-width: 768px) {
  .no-requests {
    font-size: 1rem !important;
  }
  
}